<template>
    <v-card height="462px">
        <v-card-title class="text-center pt-1">{{title}} </v-card-title>
        <v-card elevation="8" class="mx-3 " outlined >
            <v-card-subtitle class="text-center overline mt-n4"><span style="background-color:#1E1E1E" class="px-1">Camlocks</span></v-card-subtitle>
            <v-container>
                <v-layout row justify-space-around class="mt-n3" v-for="(group, key) in groups.camlocks" :key="key">
                    <div class="box" :style="hover" v-for="(viewItem, key) in $displayOrder($view[group])" :key="key">
                        <v-flex class=" pb-1" :class="$vuetify.breakpoint.lg ? ['py-0','pt-2'] : 'pa-2'">
                            <BoxValue :view="viewItem"/>
                        </v-flex>
                    </div>
                </v-layout>
            </v-container>
        </v-card>
        <v-card elevation="8" class="mx-3 mt-6" outlined>
        <v-card-subtitle class="text-center overline mt-n4"><span style="background-color:#1E1E1E" class="px-1">Secondary</span></v-card-subtitle>
        <v-container>
            <v-layout row justify-space-around class="mt-n3" v-for="(group, key) in groups.secondary" :key="key">
                <div class="box" :style="hover" v-for="(viewItem, key) in $displayOrder($view[group])" :key="key">
                    <v-flex :class="$vuetify.breakpoint.lg ? ['py-0','pt-2']  : 'pa-2'">
                        <BoxValue :view="viewItem"/>
                    </v-flex>
                </div>
            </v-layout>
            </v-container>
        </v-card>
        <v-container>
            <v-layout row justify-space-around class="" v-for="(group, key) in groups.other" :key="key">
                <div class="box" :style="hover " v-for="(viewItem, key) in $displayOrder($view[group])" :key="key">
                    <v-flex :class="$vuetify.breakpoint.lg ? ['py-0','pt-4']  : 'pa-2'">
                        <BoxValue :view="viewItem"/>
                    </v-flex>
                </div>

            </v-layout>
        </v-container>
    </v-card>
</template>

<script>
    import BoxValue from './BoxValue'
    import eventBus from "../eventBus";

    export default {
        name:'TechPower',
        components: {
           BoxValue
        },
        props:['cursor','service'],
        data () {
            return {
                groups:{},
                tech:{
                    camlocks:['techPowerPriV','techPowerPriC'],
                    secondary:['techPowerSecV','techPowerSecC'],
                    other:['techPowerOther']    
                },
                hvac:{
                    camlocks:['hvacPowerPriV','hvacPowerPriC'],
                    secondary:['hvacPowerSecV','hvacPowerSecC'],
                    other:['hvacPowerOther']    
                },
                title:''

            }   
        },
        methods:{
            showDialog(id){
                console.log("here",id)
               eventBus.$emit('details',id)
               // window.open("", "", "width=200,height=100")
            },
        },
        computed: {
            hover(){
                return {
                    '--cursor': this.cursor
                }
            },
            themeStuff(){
                if(this.$vuetify.theme.isDark){
                    return {'background-color':'#1E1E1E'}
                } else {
                    return {}
                }
            },
        },
        created(){
            if(this.service ==='tech'){
                this.groups = this.tech
                this.title = 'TECH POWER'
            } else if (this.service ==='hvac'){
                this.groups = this.hvac
                this.title = 'HVAC POWER'
            }
        }
    }
</script>

<style scoped>
    .v-card__subtitle {
        padding: 0px;
    }
    .container {
        width: 100%;
        padding-top: 2px;
        padding-bottom: 2px;
        margin-right: auto;
        margin-left: auto;
    }
    .v-application .overline {
        font-size: 1rem !important;
    }  
    .box {
        min-width:30%;
        cursor: var(--cursor);
    }
</style>