<template>
  <div class="dashboard" :style="breakpointLG">
    <!-- <perfect-scrollbar> -->
    <!-- <h3 class="subheading grey--text">Dashboard</h3> -->
    <v-container>
        <v-row justify='center' dense class="pb-1">
            <v-col cols="12"  class="pa-0">
                <div class="d-flex justify-center flex-wrap" color="transparent" flat tile>
                    <v-slide-group>
                      <v-slide-item disabled>
                        <div class="d-flex justify-space-between">
                          <Closure :view="id" :cursor="'defult'" class="mx-1" v-for="(id, key) in $displayOrder($view.closure)" :key="key"/>
                        </div>
                      </v-slide-item>
                    </v-slide-group>
                </div>
            </v-col>
        </v-row>
          <v-row justify-center dense>
            <v-col cols="12" lg="3" md="6" sm="6" xs="12">
              <PowerCard :cursor="'defult'" :service="'tech'"/>
            </v-col>
            <v-col cols="12" lg="3" md="6" sm="6" xs="12">
               <PowerCard :cursor="'defult'" :service="'hvac'"/>
            </v-col>
            <v-col cols="12" lg="3" md="6" sm="6" xs="12">
              <v-row no-gutters dense> <v-col> <HVAC :cursor="'defult'"/> </v-col> </v-row>
              <v-row class="mt-2 align-stretch"> <v-col class="py-0"> <Thermostat :cursor="'defult'"/> </v-col> </v-row>
            </v-col>
            <v-col cols="12" lg="3" md="6" sm="6" xs="12">
               <Notifications /> 
            </v-col>
        </v-row>
        <v-row justify-center dense>
          <v-col cols="12" lg="12" md='12' sm='12' xs='6' class="pa-0">
            <div class="d-flex justify-center flex-wrap" >
              
                <Temperature  v-for="(id, key) in $displayOrder($view.tempSensors)" :key="key" class="ma-1" :view="id" :cursor="'defult'"/>
              
            </div>
          </v-col>
        </v-row>
    </v-container>
    <!-- </perfect-scrollbar> -->
  </div>
</template>

<script>
import Closure from '@/components/Closure.vue'
import PowerCard from '@/components/PowerCard.vue'
import HVAC from '@/components/HVAC.vue'
import Notifications from '@/components/Notifications.vue'
import Thermostat from '@/components/Thermostat.vue'
import Temperature from '@/components/Temperature.vue'

export default {
  name: 'Dashboard',
  components: {
    Closure,
    PowerCard,
    HVAC,
    Thermostat,
    Notifications,
    Temperature,
  },
  mounted (){
    window.scrollTo(0, 0)
    //.log(this.$readings)
    
  },
  created(){
    console.log(this.$vuetify.breakpoint.name)
  },
  computed:{
    breakpointLG(){
      if( this.$vuetify.breakpoint.name === 'lg'){
        return {
          '--customWidth': "100%"
        }
      } else {
        return {
          '--customWidth': "1185px"
        }
      }
    
      
    },
  }
  
}
</script>

<style scoped>
 .container {
    width: 100%;
    padding: 1px; 
    
    /*  margin-right: 0px; 
    margin-left: 0px; */  
  } 
  .dashboard {
    overflow: hidden;
  }
  /* @media (min-width: 1264px){ 
  .container {
     max-width: 100% !important 
  } 
} */

@media (min-width: 1264px){ 
  .container {
     max-width: 1664px !important 
  } 
}


</style>